* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-weight: 300;
}

.alan {
  width: 100vw;
  height: 100vh;
  padding: 80px 0;
  text-align: center;
  font-family: "Source Sans Pro", sans-serif;
  color: white;
  font-weight: 300;
  background: #50a3a2;
}



.alan h1 {
  font-size: 40px;
  font-weight: 200;
}

input {
  border: 1px solid rgba(252, 252, 252, 0.4);
  background-color: rgba(252, 252, 252, 0.2);
  width: 250px;
  border-radius: 3px;
  font-family: "Source Sans Pro", sans-serif;
  padding: 10px 15px;
